.todo-nav-bar{background-color:rgba(254,254,254,.4);position:fixed;top:0;left:0;width:100%;padding:20px 10px}.todo-item-container{display:flex;flex-direction:column;margin-top:150px;gap:12px}.todo-item-container .todo-item{display:flex;gap:20px;justify-content:space-between;width:100%;background-color:rgba(254,254,254,.6666666667);padding:10px 20px;border-radius:20px}.todo-item-container .todo-item .icon{width:35px;height:35px;border-radius:50%;text-align:center;font-size:26px}.todo-item-container .todo-item .desc{flex-grow:1;line-height:18px}.todo-item-container .todo-item .desc :first-child{font-weight:700;font-size:16px}.todo-item-container .todo-item .desc :last-child{color:gray;font-size:12px}.todo-item-container .todo-item .upcoming{text-align:right;line-height:18px}.todo-item-container .todo-item .upcoming :first-child{font-weight:700;font-size:20px}.todo-item-container .todo-item .upcoming :last-child{font-size:12px}.todo-add-item-form{display:flex;flex-direction:column;gap:15px;padding:20px;padding-top:40px;height:100%}.todo-add-item-form .icon{font-size:40px;text-align:center;width:60px;align-self:center}.todo-add-item-form .icon input{padding:0;height:60px;font-size:44px}.todo-add-item-form .category-select{display:flex;flex-direction:column}.todo-add-item-form:last-child{margin-top:auto}