.emoji_select{--cell-size: 40px;--gap: 10px;--width: 280px;--height: 200px;--highlight: #ddd;width:var(--width);max-height:600px;touch-action:none;font-family:apple color emoji,segoe ui emoji,noto color emoji,android emoji,emojisymbols,emojione mozilla,twemoji mozilla,segoe ui symbol,Noto Color Emoji Compat,emoji,sans-serif}.emoji_select .emoji_category_swiper{width:var(--width);overflow:hidden;overflow-x:scroll;overflow-y:hidden;-webkit-overflow-scrolling:touch;position:relative}.emoji_select .emoji_category_swiper .emoji_sliding_block{position:absolute;background-color:var(--highlight);transition:all .3s;border-radius:10px;top:0px;z-index:1}.emoji_select .emoji_category_swiper::-webkit-scrollbar{display:none}.emoji_select .emoji_category_swiper .emoji_category_container{display:flex;flex-direction:row;flex-wrap:nowrap;gap:var(--gap);height:32px;position:relative;z-index:2;align-items:center}.emoji_select .emoji_category_swiper .emoji_category_container .emoji_category_item{cursor:pointer;white-space:nowrap;user-select:none;padding:4px 6px;border-radius:6px;font-size:18px}.emoji_select .emoji_container{display:grid;grid-template-columns:repeat(auto-fill, minmax(var(--cell-size), 1fr));grid-auto-rows:auto;justify-items:start;width:var(--width);max-height:var(--height);overflow-y:scroll;overflow-x:hidden}.emoji_select .emoji_container .emoji_item{user-select:none;font-size:calc(var(--cell-size)*.625);text-align:center;width:var(--cell-size);height:var(--cell-size);cursor:pointer}.emoji_select .emoji_container .emoji_item:hover{background-color:var(--highlight);border-radius:6px}.emoji_select .emoji_container .emoji_active{background-color:var(--highlight);border-radius:6px}.emoji_select .emoji_container .emoji_skin_group{display:flex;gap:var(--gap);font-size:calc(var(--cell-size)*.625)}.emoji_select .emoji_container .ant-popover-inner{padding:6px}.emoji_select .emoji_container::-webkit-scrollbar{display:none}